.main{
  width: 100%;
  height:100vh;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap:30px;
  background-color: #344151;
 font-size: 25px;

}
.heading{
  color:#0398d4;

}
.main-content{
  width:100px;
  display:flex;
  flex-direction: row;
  justify-content: center;
  background-color: white;
  padding: 3px;
  border-radius: 2px;
  gap:15px;
  color:#344151;
  font-style: bold;
  font-size: 25px;
  
} 
/* button{
  width:60px;
  font-size: 15px;
 
} */

.decreasebtn{
  border-right: 2px solid black;
  gap:12px;
  border-color: #bfbfbf;
  text-align: center;
  cursor: pointer;
}

.increasebtn{
  border-left: 2px solid black;
  gap:12px;
  border-color: #bfbfbf;
  text-align: center;
  cursor: pointer;
}
.resetbtn{
  background-color:#0398d4;
  color:white;
  cursor: pointer;
  font-size: 20px;

}